.carousel-wrapper {
  margin: 0 auto;
  width: 100%;
  height: 100%;
  position: relative;
}

.carousel {
  position: relative;
  overflow: visible;
  width: 100%;
  height: 100%;
}

.s2 .carousel-wrapper {
	background-color: #f4f3f3;
}

.s2 .carousel-image {
	padding-bottom: 40%;
}

.carousel ul {
  width: 20000em;
  position: relative;
  list-style: none;
  margin: 0;
  padding: 0;
}

.carousel ul li {
  float: left;
  position: relative;
}

.carousel-image {
	background-size: cover;
	background-position: center;
	width: 100%;
	height: 100%;
	padding-bottom: 40%;
}

.carousel ul li img {
  height: 100%;
  width: 100%;
}

.carousel ul li img {
  height: 68%;
  max-height: 68%;
  width: 100%;
}

.carousel-controls {
  z-index: 2;
  position: absolute;
  bottom: 50px;
  left: 50%;
  margin-left: -25px;
  width: 50px;
}

.carousel-controls a {
  display: block;
  float: left;
  width: 15px;
  height: 15px;
  background-position: 50% 50%;
}

.carousel-controls a.carousel-control {
  text-decoration: none;
  font-size: 32px;
  font-weight: bold;
  color: white;
}

.carousel-controls a.carousel-control:hover {
  opacity: 1;
}

.carousel-controls a.carousel-control:active,
.carousel-controls a.carousel-control:focus, {
  opacity: 0.5;
}

.carousel-controls a.prev {
  background-image: url('assets/img/arrow-left.svg');
  background-size: 21px 21px;
  background-repeat: no-repeat;
}

.carousel-controls a.next {
  background-image: url('assets/img/arrow-right.svg');
  background-size: 21px 21px;
  background-repeat: no-repeat;
  margin-left: 25px;
}

.carousel-controls a.prev {
  background-image: url('assets/img/arrow-left.svg');
}

.carousel-controls a.next {
  background-image: url('assets/img/arrow-right.svg');
}

.carousel .slide-prev .carousel-image, 
.carousel .slide-next .carousel-image{
  transition: opacity 500ms;
  opacity: 0.66;
  cursor: pointer;
}

.carousel .slide-prev .carousel-image:hover, 
.carousel .slide-next .carousel-image:hover {
  opacity: 1;
}

.about-slider {
  position:relative;
  overflow: hidden;
}

.carousel .title {
  color: black;
  font-family: 'Brown';
  font-size: 12px;
  display: block;
  text-align: center;
  text-transform: uppercase;
  margin-top: 50px;
}
.carousel .text {
  color: black;
  text-align: center;
  display: block;
  width: 50%;
  margin: 0px auto;
  margin-top: 40px;
}

/* 
 *  Owl Carousel - Animate Plugin
 */
.owl-carousel .animated {
  -webkit-animation-duration: 1000ms;
  animation-duration: 1000ms;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.owl-carousel .owl-animated-in {
  z-index: 0;
}
.owl-carousel .owl-animated-out {
  z-index: 1;
}
.owl-carousel .fadeOut {
  -webkit-animation-name: fadeOut;
  animation-name: fadeOut;
}

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

/* 
 *  Owl Carousel - Auto Height Plugin
 */
.owl-height {
  -webkit-transition: height 500ms ease-in-out;
  -moz-transition: height 500ms ease-in-out;
  -ms-transition: height 500ms ease-in-out;
  -o-transition: height 500ms ease-in-out;
  transition: height 500ms ease-in-out;
}

/* 
 *  Core Owl Carousel CSS File
 */
.owl-carousel {
  display: none;
  width: 100%;
  -webkit-tap-highlight-color: transparent;
  /* position relative and z-index fix webkit rendering fonts issue */
  position: relative;
  z-index: 1;
}
.owl-carousel .owl-stage {
  position: relative;
  -ms-touch-action: pan-Y;
}
.owl-carousel .owl-stage:after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}
.owl-carousel .owl-stage-outer {
  position: relative;
  overflow: hidden;
  /* fix for flashing background */
  -webkit-transform: translate3d(0px, 0px, 0px);
}
.owl-carousel .owl-controls .owl-nav .owl-prev,
.owl-carousel .owl-controls .owl-nav .owl-next,
.owl-carousel .owl-controls .owl-dot {
  cursor: pointer;
  cursor: hand;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.owl-carousel.owl-loaded {
  display: block;
}
.owl-carousel.owl-loading {
  opacity: 0;
  display: block;
}
.owl-carousel.owl-hidden {
  opacity: 0;
}
.owl-carousel .owl-refresh .owl-item {
  display: none;
}
.owl-carousel .owl-item {
  position: relative;
  min-height: 1px;
  float: left;
  -webkit-backface-visibility: hidden;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.owl-carousel .owl-item img {
  display: block;
  width: 100%;
  -webkit-transform-style: preserve-3d;
}
.owl-carousel.owl-text-select-on .owl-item {
  -webkit-user-select: auto;
  -moz-user-select: auto;
  -ms-user-select: auto;
  user-select: auto;
}
.owl-carousel .owl-grab {
  cursor: move;
  cursor: -webkit-grab;
  cursor: -o-grab;
  cursor: -ms-grab;
  cursor: grab;
}
.owl-carousel.owl-rtl {
  direction: rtl;
}
.owl-carousel.owl-rtl .owl-item {
  float: right;
}

/* No Js */
.no-js .owl-carousel {
  display: block;
}

/* 
 *  Owl Carousel - Lazy Load Plugin
 */
.owl-carousel .owl-item .owl-lazy {
  opacity: 0;
  -webkit-transition: opacity 400ms ease;
  -moz-transition: opacity 400ms ease;
  -ms-transition: opacity 400ms ease;
  -o-transition: opacity 400ms ease;
  transition: opacity 400ms ease;
}
.owl-carousel .owl-item img {
  transform-style: preserve-3d;
}

/* 
 *  Owl Carousel - Video Plugin
 */
.owl-carousel .owl-video-wrapper {
  position: relative;
  height: 100%;
  background: #000;
}
.owl-carousel .owl-video-play-icon {
  position: absolute;
  height: 80px;
  width: 80px;
  left: 50%;
  top: 50%;
  margin-left: -40px;
  margin-top: -40px;
  background: url("owl.video.play.png") no-repeat;
  cursor: pointer;
  z-index: 1;
  -webkit-backface-visibility: hidden;
  -webkit-transition: scale 100ms ease;
  -moz-transition: scale 100ms ease;
  -ms-transition: scale 100ms ease;
  -o-transition: scale 100ms ease;
  transition: scale 100ms ease;
}
.owl-carousel .owl-video-play-icon:hover {
  -webkit-transition: scale(1.3, 1.3);
  -moz-transition: scale(1.3, 1.3);
  -ms-transition: scale(1.3, 1.3);
  -o-transition: scale(1.3, 1.3);
  transition: scale(1.3, 1.3);
}
.owl-carousel .owl-video-playing .owl-video-tn,
.owl-carousel .owl-video-playing .owl-video-play-icon {
  display: none;
}
.owl-carousel .owl-video-tn {
  opacity: 0;
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  -webkit-background-size: contain;
  -moz-background-size: contain;
  -o-background-size: contain;
  background-size: contain;
  -webkit-transition: opacity 400ms ease;
  -moz-transition: opacity 400ms ease;
  -ms-transition: opacity 400ms ease;
  -o-transition: opacity 400ms ease;
  transition: opacity 400ms ease;
}
.owl-carousel .owl-video-frame {
  position: relative;
  z-index: 1;
}
